import React from 'react';
import { Link } from 'react-router-dom';
import dogTraining from '../icon/dog-trainingOrange.png';
import veterinarian from '../icon/veterinarianOrange.png';
import dogHouse from '../icon/dog-houseOrange.png';
import dogWallker from '../icon/walking-with-dogOrange.png';
import scissors from '../icon/scissorsOrange.png';
import petShop from '../icon/pet-shopOrange.png';

const ProfessionalsType = () => (
  <section className="choose-professionals">
    <div className="container-choose-professionals">
      <h2>בחירת בעלי מקצוע</h2>
      <div className="wrap-professionals-icon">
        <div className="professionals-icon">
          <Link to="#">
            <img src={dogTraining} alt="training dog" />
            <h3>מאלפים</h3>
          </Link>
        </div>
        <div className="professionals-icon">
          <Link to="#">
            <img src={veterinarian} alt="veterinarian dog" />
            <h3>וטרינרים</h3>
          </Link>
        </div>
        <div className="professionals-icon">
          <Link to="#">
            <img src={dogHouse} alt="dog house" />
            <h3>פנסיונים</h3>
          </Link>
        </div>
        <div className="professionals-icon">
          <Link to="#">
            <img src={dogWallker} alt="walking with dog" />
            <h3>דוגווקרים</h3>
          </Link>
        </div>
        <div className="professionals-icon">
          <Link to="#">
            <img src={scissors} alt="scissors dog" />
            <h3>מספרות</h3>
          </Link>
        </div>
        <div className="professionals-icon">
          <Link to="#">
            <img src={petShop} alt="dog shop" />
            <h3>חנויות</h3>
          </Link>
        </div>
      </div>
    </div>
  </section>
);

export default ProfessionalsType;
