import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import './professionals.css';

import ProfessionalsType from './professionalsType';
import Professional from './Professional';

const Professionals = () => {
  return (
    <Fragment>
      <ProfessionalsType />
      <section className="sort-proffesionals">
        <div className="headline-sort">
          <p>סדר לפי:</p>
          <Link to="#">דירוג</Link>
          <p>|</p>
          <Link to="#">מרחק</Link>
          <p>|</p>
          <Link to="#">שם</Link>
        </div>
      </section>
      <section className="main-professionals">
        <div className="container-main-professionals">
          <div className="filter-pro">
            <div className="filter-title">
              <h1>וטרינרים</h1>
              <Link to="#">&#10006;</Link>
            </div>
            <div className="store-open">
              <label className="container">
                <input type="checkbox" checked="checked" />
                <span className="checkmark"></span>
                <p>פתוח עכשיו</p>
              </label>
            </div>
            <div className="area">
              <h4>איזור:</h4>
              <ul className="professionals-area">
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>חיפה והקריות</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" checked="checked" />
                    <span className="checkmark"></span>
                    <p>ת"א והמרכז</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" checked="checked" />
                    <span className="checkmark"></span>
                    <p>השרון</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>השפלה</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>הצפון</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>הדרום</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>ירושלים</p>
                  </label>
                </li>
              </ul>
            </div>
            <div className="sort">
              <h4>סינונים נוספים:</h4>
              <ul className="professionals-area">
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>מומחי רקסי</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" checked="checked" />
                    <span className="checkmark"></span>
                    <p>חוות דעת</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>פתוח בלילה</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>פתוח בשבת</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>שירות בשפה הערבית</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" />
                    <span className="checkmark"></span>
                    <p>שירות בשפה האנגלית</p>
                  </label>
                </li>
                <li className="container-li">
                  <label className="container">
                    <input type="checkbox" checked="checked" />
                    <span className="checkmark"></span>
                    <p>נגישות לאנשים עם מוגבלות</p>
                  </label>
                </li>
              </ul>
            </div>
          </div>
          <div className="display-pro">
            <Professional />
            <Professional />
            <Professional />
            <Professional />
            <Professional />
          </div>
        </div>
      </section>
    </Fragment>
  );
};

export default Professionals;
