import React from 'react';
import { Link } from 'react-router-dom';
import { API } from '../config';

import proImage from '../img/images.png';
import rexiMedal from '../icon/medal.png';
import proLocaion from '../icon/map-location.png';
import Star from './../const/star';

const ProDetails = () => (
  <section className="professional-details">
    <div className="professional-container">
      <div clss="professional-image">
        <img
          className="pro-image"
          src={`${API}/professional/logo/5e2dc620ade352bccc2f3d13`}
          alt="professionals"
        />
      </div>
      <div className="professional-details-wrap">
        <div className="professionals-headline">
          <h1>הולכת כלבים DogMen</h1>
          <div className="pro-special-rexi">
            <img src={rexiMedal} alt="rexi medal" />
            <h3>מומחה רקסי</h3>
          </div>
        </div>
        <div className="professional-detail">
          <p>
            העבודה עם כלבים הובילה אותנו להבנת צרכיו הפיזיים והמנטאליים של כל
            כלב בפרט. אנו מאמינים בשפה משותפת עם הכלב, האילוף והעבודה עם הכלב
            מהנים לאורך כל הדרך, לכלב ולבעלים, עד שתהיה לכם שפה משותפת. השפה
            הייחודית לכם היא שיוצרת מערכת יחסים יציבה המבוססת על אמון ומשם -
            הבנת המקום ההיררכי במשפחה.
          </p>
        </div>
        <div className="pro-options">
          <ul>
            <li>פתוח בשבת</li>
            <li>פתוח בלילה</li>
            <li>שירות בשפה הערבית</li>
            <li>שירות בשפה האנגלית</li>
            <li>נגישות לאנשים עם מוגבלות</li>
          </ul>
        </div>
        <div className="pro-details-review">
          <div className="pro-details-review-stars">
            <Star size="star-24" color="star-red" />
            <Star size="star-24" color="star-red" />
            <Star size="star-24" color="star" />
            <Star size="star-24" color="star" />
            <Star size="star-24" color="star" />
          </div>
          <h3>ע"י 5 מדרגים</h3>
        </div>
      </div>
      <div className="pro-hour-open">
        <table>
          <tr>
            <th className="day">א:</th>
            <th className="hour">8:00-14:00</th>
            <th className="hour">16:00-18:30</th>
          </tr>
          <tr>
            <th className="day">ב:</th>
            <th className="hour">8:00-14:00</th>
            <th className="hour">16:00-18:30</th>
          </tr>
          <tr>
            <th className="day">ג:</th>
            <th className="hour">8:00-16:00</th>
          </tr>
          <tr>
            <th className="day">ד:</th>
            <th className="hour">8:00-14:00</th>
            <th className="hour">16:00-18:30</th>
          </tr>
          <tr>
            <th className="day">ה:</th>
            <th className="hour">8:00-14:00</th>
            <th className="hour">16:00-18:30</th>
          </tr>
          <tr>
            <th className="day">ו:</th>
            <th className="hour">8:00-12:30</th>
          </tr>
          <tr>
            <th className="day">ש:</th>
            <th className="hour">סגור</th>
          </tr>
        </table>

        <div className="pro-address">
          <div className="pro-location">
            <Link to="#">
              <img src={proLocaion} alt="location" />
            </Link>
            <h3>הבוסתן 67, בני ברק פינת בית לחם הגלילית</h3>
          </div>
          <h2>073-3528971</h2>
        </div>
      </div>
    </div>
  </section>
);

export default ProDetails;
