import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { API } from '../config';
import g1 from '../img/vet1.jpg';
import g2 from '../img/vet2.jpg';
import g3 from '../img/vet3.jpg';

const ProGallery = () => (
  <Fragment>
    <div className="last-article-hedline">
      <h2>גלריית תמונות</h2>
    </div>
    <section className="gallery">
      <div className="gallery-container">
        <div className="gallery-slider 1">
          <img
            src={`${API}/professional/g1/5e2dc620ade352bccc2f3d13`}
            alt="dog veterinarian"
          />
        </div>

        <div className="gallery-slider 2">
          <img src={g2} alt="dog veterinarian" />
        </div>
        <div className="gallery-slider 3">
          <img src={g3} alt="dog veterinarian" />
          <div className="next">
            <Link to="#">❯</Link>
          </div>
        </div>

        <div className="prev">
          <Link to="#">❮</Link>
        </div>
      </div>
    </section>
  </Fragment>
);

export default ProGallery;
