import React from 'react';
import { Link } from 'react-router-dom';

import professionalImg from '../img/images.png';
import rexiPro from '../icon/medal.png';
import Star from './../const/star';
import HalfStar from '../const/half-star';

const Professional = () => (
  <section className="all-professional">
    <Link to="/pro">
      <div className="pro">
        <img src={professionalImg} alt="professional" />
        <div className="pro-wrap">
          <div className="pro-headline">
            <h2>הולכת כלבים DogMen</h2>
            <div className="special-rexi">
              <img src={rexiPro} alt="rexi medal" />
              <h3>מומחה רקסי</h3>
            </div>
          </div>
          <div className="pro-details">
            <p>
              העבודה עם כלבים הובילה אותנו להבנת צרכיו הפיזיים והמנטאליים של כל
              כלב בפרט. אנו מאמינים בשפה משותפת עם הכלב, האילוף והעבודה עם הכלב
              מהנים לאורך כל הדרך, לכלב ולבעלים, עד שתהיה לכם שפה משותפת. השפה
              הייחודית לכם היא שיוצרת מערכת יחסים יציבה המבוססת על אמון ומשם -
              הבנת המקום ההיררכי במשפחה.
            </p>
            <div className="professional-address">
              <h3>הבוסתן 67, בני ברק</h3>
              <h2>073-3528971</h2>
              <h4>פתוח עכשיו</h4>
            </div>
          </div>
          <div className="professional-review-avg">
            <div className="professional-review-avg-stars">
              <Star color="star-red" size="star-16" />
              <HalfStar size="star-16" />
              <Star size="star-16" color="star" />
              <Star size="star-16" color="star" />
              <Star size="star-16" color="star" />
            </div>
            <h3>ע"י 5 מדרגים</h3>
          </div>
        </div>
      </div>
    </Link>
  </section>
);

export default Professional;
